<!-- Ë¥™ÂêÉËõá -->
<template>
	<div id="Ggreedysnake">
		<div class="tetris-wrapper" @click="test" ref="test" @swipeup="changeMoveDirection(0)"
			@swiperight="changeMoveDirection(1)" @swipedown="changeMoveDirection(2)"
			@swipeleft="changeMoveDirection(3)">
			<div class="game-wrapper" ref="container">
				<div class="block-group" v-for="(item, index) in structureArray" :key="index">
					<div class="block" v-for="(block, blockIndex) in item" :key="blockIndex"
						:class="{green: block.state === 1, red: block.state === 2, blue: block.state === 3, black: block.state === 4}">
					</div>
				</div>
			</div>
			<div class="container" v-if="gameOver">
				<div class="dialog">
					<p class="once-again">Êú¨ËΩÆÂàÜÊï∞Ôºö{{score}}ÂàÜ</p>
					<p class="once-again">ÁúüÂ•ΩÁé©ÔºÅ</p>
					<p class="once-again">ÂÜçÊù•‰∏ÄÊ¨°~~</p>
					<el-button class="once-again-btn" @click="reload">ÂºÄÂßã</el-button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		inject: ['reload'],
		mounted() {
			this.initializationGrid(); // ÂàùÂßãÂåñÁΩëÊ†º
			this.initializationSnake(); // ÂàùÂßãÂåñËõá
			this.initializationTimer(); // ÂàùÂßãÂåñÂÆöÊó∂Âô®
			this.generateFood(); // ÁîüÊàêÈ£üÁâ©
			this.addKeyboardEvents(); // Ê∑ªÂä†ÈîÆÁõòÊåâ‰∏ã‰∫ã‰ª∂
		},
		destroyed() {
			this.clearTimer();
		},
		computed: {
			longitudinal() { // Ëé∑ÂèñÂÆπÂô®ÂÆΩÈ´òÊØî,ËÆ°ÁÆóÁ∫µÂùêÊ†áÊñπÊ†ºÊï∞
				let boxWidth = this.$refs.container.clientWidth;
				let boxHeight = this.$refs.container.clientHeight;
				let longitudinal = parseInt((boxHeight / boxWidth) * this.transverse);
				return longitudinal;
			}
		},
		data() {
			return {
				timer: null, // Â≠òÂÇ®ÂÆöÊó∂Âô®
				interval: 200, // ÂÆöÊó∂Èó¥ÈöîÊó∂Èó¥
				transverse: 16, // Ê®™ÂùêÊ†áÊñπÊ†ºÊï∞Èáè
				structureArray: [], // Ê∏∏ÊàèÁîªÂ∏ÉÁªìÊûÑÊï∞ÁªÑ
				moveDirection: 1, // ËõáÁöÑÁßªÂä®ÊñπÂêëÔºå0„ÄÅ1„ÄÅ2„ÄÅ3Ôºå‰∏äÂè≥‰∏ãÂ∑¶
				nextMoveDirection: 1, // Â∞ÜË¶ÅÁßªÂä®ÁöÑÊñπÂêëÔºå0„ÄÅ1„ÄÅ2„ÄÅ3Ôºå‰∏äÂè≥‰∏ãÂ∑¶
				snakeHead: {}, // ËõáÂ§¥ÁöÑÂùêÊ†á
				snakeTail: {}, // ËõáÂ∞æÂùêÊ†á
				foodCoordinate: {}, // È£üÁâ©ÂùêÊ†á
				gameOver: false,
				score: 200
			}
		},
		methods: {
			test() {
				console.log(this.$refs.container.clientHeight);
				console.log(this.$refs.container.clientWidth);
				console.log(this.longitudinal);
			},
			changeMoveDirection(direction) { // ÊîπÂèòËõáÁöÑËøêÂä®ÊñπÂêë
				let judge = direction - this.moveDirection; // Âà§Êñ≠ÈùûÊ≥ïÁöÑËõáÂ§¥ËΩ¨Âêë
				if (judge === 2 || judge === -2) return;
				this.nextMoveDirection = direction;
			},
			mobileRule() { // ËõáÁöÑÁßªÂä®ËßÑÂàôÂÆö‰πâ
				let {
					x,
					y
				} = this.snakeHead; // ÂΩìÂâçËõáÂ§¥ÁöÑÂùêÊ†á
				let headCode = this.structureArray[y][x].code; // Ëé∑ÂèñÂΩìÂâçËõáÂ§¥ÁöÑcode,Áî®Êù•ÁîüÊàêÊñ∞ËõáÂ§¥Êó∂++‰ΩøÁî®
				this.structureArray[y][x].state = 1; // ÂèñÊ∂àÂéüÊù•ÁöÑËõáÂ§¥Áä∂ÊÄÅ
				switch (this.nextMoveDirection) { // ÁßªÂä®ÊñπÂêëÔºåËÆ°ÁÆóÁßªÂä®ÂêéÁöÑÊ®™Á∫µÂùêÊ†á
					case 0:
						y--;
						break;
					case 1:
						x++
						break;
					case 2:
						y++;
						break;
					case 3:
						x--;
						break;
					default:
						throw new Error('ËõáÁöÑÁßªÂä®ËßÑÂàôÂà§Êñ≠Âá∫Èîô');
						break;
				}
				this.structureArray[y].splice(x, 1, {
					state: 2,
					code: ++headCode
				}); // ÁîüÊàêÊñ∞ÁöÑËõáÂ§¥
				this.snakeHead = {
					x,
					y
				};
				this.moveDirection = this.nextMoveDirection;
				this.whetherEatFood(); // Âà§Êñ≠ÊòØÂê¶ÂêÉÂà∞È£üÁâ©ÔºàËõáÂ§¥ÂíåÈ£üÁâ©ÂùêÊ†áÊòØÂê¶ÈáçÂêàÔºâ
			},
			whetherEatFood() { // ÊòØÂê¶ÂêÉÂà∞È£üÁâ©Âà§Êñ≠
				let state = JSON.stringify(this.snakeHead) === JSON.stringify(this.foodCoordinate); // Âà§Êñ≠È£üÁâ©ÂíåËõáÂ§¥ÊòØÂê¶Âú®‰∏Ä‰∏™ÂùêÊ†á‰∏ä
				if (state) { // ÂêÉÂà∞‰∫Ü
					this.generateFood(); // ÁîüÊàêÈ£üÁâ©
					this.integralRule(); // Ëµ∞ÁßØÂàÜ
					this.increaseDifficulty(); // Â¢ûÂä†ÈöæÂ∫¶
				} else { // Ê≤°ÂêÉÂà∞È£üÁâ©
					this.generateSnakeTail(); // ÈáçÊñ∞ÁîüÊàêËõáÂ∞æ
				}
			},
			generateFood() { // ÁîüÊàêÈ£üÁâ©,ÁîüÊàêÈöèÊú∫È£üÁâ©ÂùêÊ†á
				let x = Math.floor(Math.random() * this.transverse); // ÁîüÊàêÈöèÊú∫Êï∞
				let y = Math.floor(Math.random() * this.longitudinal); // ÁîüÊàêÈöèÊú∫Êï∞
				if (this.structureArray[y][x].state === 0) { // ÊòØÁ©∫‰ΩçÁΩÆ
					this.foodCoordinate = {
						x,
						y
					}; // Êñ∞ÁöÑÈ£üÁâ©ÂùêÊ†á
					this.structureArray[y].splice(x, 1, {
						state: 4,
						code: Infinity
					}); // ‰øÆÊîπÂùêÊ†áÁä∂ÊÄÅÂÄº,codeÊó†ÈôêÂ§ßÁî®Êù•ËÆ°ÁÆóËõáÂ∞æÊó∂‰∏ç‰ºöÂá∫Èîô
				} else { // ‰ΩçÁΩÆ‰∏ç‰∏∫Á©∫ÔºåÈáçÊñ∞ÁîüÊàê
					this.generateFood();
				}
			},
			integralRule() { // ÁßØÂàÜËßÑÂàô
			},
			increaseDifficulty() { // Â¢ûÂä†ÈöæÂ∫¶ËßÑÂàô
				if (this.interval <= 100) return;
				this.interval -= 4;
				this.clearTimer();
				this.initializationTimer();
			},
			generateSnakeTail() { // ÈáçÊñ∞ÁîüÊàêËõáÂ∞æ(Âú®Ê≤°ÊúâÂêÉÂà∞È£üÁâ©ÁöÑÊÉÖÂÜµ‰∏ãË∞ÉÁî®)
				let {
					x,
					y
				} = this.snakeTail; // Ëé∑ÂèñËõáÂ∞æÂùêÊ†á
				// let tailCode = this.structureArray[y][x].code + 1; // ËÆ°ÁÆóÂ∞ÜË¶Å‰Ωú‰∏∫ËõáÂ∞æÁöÑcode -------- ËøôÈáåcodeÂèñÈîô‰∫ÜÔºåËøôÊ†∑ÁöÑËØùÂèñÂæóÊòØËõáÂ§¥ÁöÑÔºåÊîπÊàêÂèñ‰∏ä‰∏ãÂ∑¶Âè≥ÊúÄÂ∞èÁöÑcode
				// ÈîÄÊØÅ‰πãÂâçÁöÑËõáÂ∞æ(Ëã•ÂΩìÂâçËõáÂ∞æËõáÂ∞æ‰ΩçÁΩÆÊòØËõáÂ§¥ÔºåÂàô‰∏çÂÅöÂ§ÑÁêÜÔºåÂõ†‰∏∫Â§¥ÂÖàÁîüÊàê)
				if (this.structureArray[y][x].state !== 2) this.structureArray[y].splice(x, 1, {
					state: 0
				});
				// Êé•‰∏ãÊù•ÈáçÊñ∞ÂÆö‰ΩçËõáÂ∞æÂùêÊ†á
				/*if (this.structureArray[y + 1][x].state && tailCode === this.structureArray[y + 1][x].code) y++;
				else if (this.structureArray[y - 1][x].state && tailCode === this.structureArray[y - 1][x].code) y--;
				else if (this.structureArray[y][x + 1].state && tailCode === this.structureArray[y][x + 1].code) x++;
				else if (this.structureArray[y][x - 1].state && tailCode === this.structureArray[y][x - 1].code) x--;*/
				// else throw new Error('ÈáçÊñ∞ÁîüÊàêËõáÂ∞æÈîôËØØ')
				// Âèñ‰∏ä‰∏ãÂ∑¶Âè≥codeÊúÄÂ∞èÁöÑÊòØÊñ∞ËõáÂ∞æ
				let top = (y - 1 >= 0) && this.structureArray[y - 1][x].state ? this.structureArray[y - 1][x].code :
					Infinity;
				let bottom = (y + 1 < this.longitudinal) && this.structureArray[y + 1][x].state ? this.structureArray[y +
					1][x].code : Infinity;
				let left = (x - 1 >= 0) && this.structureArray[y][x - 1].state ? this.structureArray[y][x - 1].code :
					Infinity;
				let right = (x + 1 < this.transverse) && this.structureArray[y][x + 1].state ? this.structureArray[y][x +
					1
				].code : Infinity;
				let min = Math.min(top, bottom, left, right);
				switch (min) {
					case top:
						y--;
						break;
					case bottom:
						y++;
						break;
					case left:
						x--;
						break;
					case right:
						x++;
						break;
					default:
						console.log('ÈáçÊñ∞ÁîüÊàêËõáÂ∞æÂá∫Èîô‰∫Ü');
						break;
				}
				this.snakeTail = {
					x,
					y
				}; // ‰øùÂ≠òËõáÂ∞æÂùêÊ†á
				this.structureArray[y][x].state = 3; // Â∞ÜÊ≠§ÂùêÊ†áÂÆö‰πâ‰∏∫ËõáÂ∞æ
			},
			deathRule() { // ËõáÁöÑÊ≠ª‰∫°ËßÑÂàôÂà§Êñ≠
				let {
					x,
					y
				} = this.snakeHead;
				switch (this.nextMoveDirection) { // ÁßªÂä®ÊñπÂêë(Á¢∞Â£ÅÂà§Êñ≠ÂíåÁ¢∞ËõáË∫´Âà§Êñ≠)
					case 0:
						if (y - 1 < 0) return false;
						else if (this.structureArray[y - 1][x].state === 1) return false;
						break;
					case 1:
						if (x + 1 > this.transverse - 1) return false;
						else if (this.structureArray[y][x + 1].state === 1) return false;
						break;
					case 2:
						if (y + 1 > this.longitudinal - 1) return false;
						else if (this.structureArray[y + 1][x].state === 1) return false;
						break;
					case 3:
						if (x - 1 < 0) return false;
						else if (this.structureArray[y][x - 1].state === 1) return false;
						break;
					default:
						throw new Error('ËõáÁöÑÊ≠ª‰∫°ËßÑÂàôÂà§Êñ≠ÈîôËØØ');
						break;
				}
				return true;
			},
			deathTreatment() { // ËõáÁöÑÊ≠ª‰∫°Â§ÑÁêÜ
				this.clearTimer();
				this.gameOver = true;
				this.score = 200 - this.interval;
			},
			initializationGrid() { // ÂàùÂßãÂåñÁΩëÊ†º
				this.structureArray = [];
				for (let i = 0; i < this.longitudinal; i++) {
					this.structureArray.push([]);
					for (let j = 0; j < this.transverse; j++) {
						this.structureArray[i].push({
							state: 0 // 0‰∏∫Á©∫Ôºå1‰∏∫ËõáË∫´Ôºå2‰∏∫ËõáÂ§¥Ôºå3‰∏∫ËõáÂ∞æÔºå4‰∏∫È£üÁâ©
						});
					}
				}
			},
			initializationSnake() { // ÂàùÂßãÂåñËõá
				this.structureArray[2].splice(4, 1, {
					state: 2,
					code: 2
				});
				this.structureArray[2].splice(3, 1, {
					state: 1,
					code: 1
				});
				this.structureArray[2].splice(2, 1, {
					state: 3,
					code: 0
				});
				this.snakeHead = {
					x: 4,
					y: 2
				}; // ËõáÂ§¥ÂùêÊ†á
				this.snakeTail = {
					x: 2,
					y: 2
				}; // ËõáÂ∞æÂùêÊ†á
			},
			initializationTimer() { // ÂàùÂßãÂåñÂÆöÊó∂Âô®
				this.timer = setInterval(() => {
					// Â§ÑÁêÜ
					if (this.deathRule()) { // Ê≠ª‰∏ç‰∫Ü
						this.mobileRule(); // Ëµ∞ÁßªÂä®
					} else { // Ê≠ªÊéâ‰∫Ü
						this.deathTreatment();
					}
				}, this.interval);
			},
			clearTimer() { // Ê∏ÖÁêÜÂÆöÊó∂Âô®
				clearInterval(this.timer);
			},
			addKeyboardEvents() { // Ê∑ªÂä†ÂÖ®Â±ÄÈîÆÁõòÊåâ‰∏ã‰∫ã‰ª∂
				let _this = this;
				document.onkeydown = function(e) {
					let key = window.event.keyCode;
					switch (key) {
						case 38:
							_this.changeMoveDirection(0);
							break;
						case 39:
							_this.changeMoveDirection(1);
							break;
						case 40:
							_this.changeMoveDirection(2);
							break;
						case 37:
							_this.changeMoveDirection(3);
							break;
						case 13:
							_this.reload();
							break;
					}
				}
			}
		}
	}
</script>

<style scoped>
	#Ggreedysnake {
		width: 100%;
		height: calc(100vh - 80px);
		background-image: url('../assets/backgrounds/ÊûÅÂÖâ.jpg');
		background-size: 100% 100%;
	}

	.tetris-wrapper {
		position: absolute;
		width: 700px;
		height: 600px;
		overflow: auto;
		background-color: rgba(255, 255, 255, 1.0);
	}

	.game-wrapper {
		position: absolute;
		top: 0rem;
		bottom: 0rem;
		left: 0rem;
		right: 0rem;
		display: flex;
		flex-direction: column;
		background-color: #303133;
	}

	.block-group {
		flex: 1;
		position: relative;
		display: flex;
	}

	.block {
		flex: 1;
		border: 0.01rem solid #909399;
	}

	.green {
		background-color: #67C23A;
	}

	.red {
		background-color: #F56C6C;
	}

	.blue {
		background-color: #409EFF;
	}

	.black {
		background-color: #E6A23C;
	}

	.container {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: rgba(0, 0, 0, 0.3);
		text-align: center;
		font-size: 0;
		white-space: nowrap;
		overflow: auto;
	}

	.container:after {
		content: "";
		display: inline-block;
		height: 100%;
		vertical-align: middle;
	}

	.dialog {
		width: 400px;
		height: 300px;
		background: rgba(255, 255, 255, 0.5);
		box-shadow: 3px 3px 6px 3px rgba(0, 0, 0, 0.3);
		display: inline-block;
		vertical-align: middle;
		text-align: left;
		font-size: 28px;
		color: #fff;
		font-weight: 600;
		border-radius: 10px;
		white-space: normal;
		text-align: center;
	}
</style>
